import { signInAction } from "./actions"
import { push } from "connected-react-router"
import {auth, db ,FirebaseTimestamp} from '../../firebase/index'

export const signIn = (
    email,password
) => {
    return async (dispatch, getState) => {
        const state = getState() // Reduxstate
        const isSignedIn = state.users.isSignedIn // ReduxstateusersisSignedIn

        if(!isSignedIn){
            const url = 'https://api.github.com/users/deatiger'
            const response = await fetch(url) // fetchawaitfetch
                                    .then(res => res.json())
                                    .catch(() => null)

            const  username = response.login

            dispatch(signInAction({
                isSignedIn: true,
                uid:"00001",
                userName:username
            }))
            dispatch(push('/'))
        }
    }
}

export const signUp = (username , email , password , confirmPassword)=> {
    return async (dispatch) => {
        // Validetion
        if(username === "" || email === "" || password === "" || confirmPassword === ""){
            alert("")
            return false //signup

        }

        if(password !== confirmPassword){
            alert("")
            return false
        }

        // createUserWithEmailAndPasswrd
        return auth.createUserWithEmailAndPasswrd(email , password) // auth=firebase
            .then(result =>{ // 
                const user = result.user

                // 
                if(user){
                    const uid = user.uid // firebaseAthenticatinUid
                    const timestamp = FirebaseTimestamp.now() // nowfirebase

                    const userInitalDate ={
                        created_at: timestamp, // 
                        email: email, // email
                        role: "customer", // 
                        uid: uid, // uid
                        update_at: timestamp, // 
                        username: username // 
                    }

                    // users
                    db.collection('users').doc(uid).state(userInitalDate) // uid
                         .then(() => {
                            dispatch(push('/'))// 
                         })
                }
        })
    }
}